import { Component, ElementRef, EventEmitter, OnInit, Output, Renderer2, ViewChild } from '@angular/core';
import { pages } from '../pages';

@Component({
  selector: 'app-header',
  templateUrl: './header.component.html',
  styleUrls: ['./header.component.scss']
})
export class HeaderComponent implements OnInit {
  pages = pages;
  sideNavOpen = false;
  
  @ViewChild('sideNav')
  sideNav!: ElementRef;
  @ViewChild('navToggle')
  navToggle!: ElementRef;
  
  constructor(private renderer: Renderer2) {
    /**
     * This events get called by all clicks on the page
     */
    this.renderer.listen('window', 'click',(e:Event)=>{
        if(e.target !== this.sideNav.nativeElement && // Don't click side nav element
          e.target !== this.navToggle.nativeElement && // Don't click hamburger area
          this.sideNavOpen===true){
            this.sideNavOpen=false;
            console.log("bad boy");
        }
        console.log(e.target)
        console.log(this.sideNav.nativeElement)
        console.log(e.target === this.sideNav.nativeElement)
    });
  }

  toggle(){
    this.sideNavOpen = !this.sideNavOpen;
    console.log(this.sideNavOpen);
  }

  
  ngOnInit(): void {
  }

}
